import { useEffect } from 'react'
import { useParams, useHistory } from 'react-router-dom'

import { useFirebase } from "@meddbriefer/mdb-firebase"
import { deleteCommentDocument } from "@meddbriefer/scenario-data/index"


export default function DeleteCommentLog() {
  const {db} = useFirebase()
  const { logID } = useParams()
  let history = useHistory()

  useEffect(
    () => {
      deleteCommentDocument(db, logID)
      history.goBack()
    },
    [db, logID, history]
  )

  return (null)
}