
import {
    HashRouter as Router,
    Switch,
} from "react-router-dom"

import { AuthProvider, PrivateRoute } from "@meddbriefer/mdb-auth";
import { useFirebaseProject, FirebaseContext } from "@meddbriefer/mdb-firebase"

import {LogList, AnalyzeDebriefing, DebLog, DeleteCommentLog, DeleteObsLogs, UnauthedLayout} from "@meddbriefer/cond1-debriefing-ui"

import { environment } from "./environments/environment";


const App = (props) => {
    const {auth, db } = useFirebaseProject(environment.firebaseProject)
    return (
      <FirebaseContext.Provider
        value={{
            auth,
            db
        }}
      >
        <AuthProvider>
          <Router>
            <Switch>
              <PrivateRoute
                exact
                path="/logs/:logID"
                authFlowLayout={UnauthedLayout}
                component={AnalyzeDebriefing}
              />
              <PrivateRoute
                exact
                path="/observer/delete/:logID"
                authFlowLayout={UnauthedLayout}
                component={DeleteObsLogs}
              />
              <PrivateRoute
                exact
                path="/comment/display/:logID"
                authFlowLayout={UnauthedLayout}
                component={DebLog}
              />
              <PrivateRoute
                exact
                path="/comment/delete/:logID"
                authFlowLayout={UnauthedLayout}
                component={DeleteCommentLog}
              />
              {/*default (catchall) route*/}
              <PrivateRoute
                match
                path="/"
                authFlowLayout={UnauthedLayout}
                component={LogList}
              />
            </Switch>
          </Router>
        </AuthProvider>
      </FirebaseContext.Provider>
    )
}


export default App;