import { useEffect, useState } from "react"
import { Link } from "react-router-dom";
import { Table } from "reactstrap";

import { useFirebase } from "@meddbriefer/mdb-firebase"
import { getDebriefCommentLogs, getLogs, getPubScenarios } from "@meddbriefer/scenario-data/index"

//import MDBNavBar from '../components/MDBNavBar';


export default function LogList() {
    const {db} = useFirebase()
    const [logs, setLogs] = useState(undefined)
    const [scenarios, setScenarios] = useState(undefined)
    const [comments, setComments] = useState(undefined)
    const [versionNames, setVersionNames] = useState(undefined)
    /* const [logsLoaded, setLogsLoaded] = useState([])
    const [commentsLoaded, setCommentsLoaded] = useState([])
    const [allLoaded, setAllLoaded] = useState(false) */

    let scenarioNameTable = {}

    async function createVersionNameTable () {
        scenarios.forEach( entry => {
            scenarioNameTable[entry.id] = entry.version
        })
        return scenarioNameTable
    }

    function getVersionName (scenarioID) {
        return versionNames[scenarioID]
    }


    useEffect(
        () => {
            getLogs(db)
            .then(result => {
                setLogs(result)
                // console.log(result)
                //setLogsLoaded(true)
            })
            .catch(error => console.log('error fetching logs', error))
        },
        [db]
    )


    useEffect(
        () => {
            !!logs && getDebriefCommentLogs(db)
            .then(result => {
                setComments(result)
                //setCommentsLoaded(true)
            })
            .catch(error => console.log('error fetching logs', error))
        },
        [db, logs]
    )

    useEffect(
        () => {
            !!comments && getPubScenarios(db)
            .then(result => {
                setScenarios(result)
                //setAllLoaded(true)
            })
            .catch(error => console.log('error fetching published scenarios', error))
        },
        // eslint-disable-next-line react-hooks/exhaustive-deps
        [db, comments]
    )

    useEffect(
        () => {
            !!scenarios && createVersionNameTable()
            .then(result => {
                setVersionNames(result)
            })
            .catch(error => console.log('error setting up scenario version name table', error))
        },
        // eslint-disable-next-line react-hooks/exhaustive-deps
        [scenarios]
    )

    if (!versionNames) {
        return (<h4>fetching log data</h4>)
    }

    return (
        <Table striped={true} borderless size="sm" hover>
            <thead><tr><th>Existing Observer Logs</th></tr></thead>
            <tbody>
                <tr>
                    <th>Filename</th>
                    <th>Scenario</th>
                    <th>Scenario_Version</th>
                    <th>Creator</th>
                    <th>Timestamp</th>
                </tr>
                {logs.map(log => (
                    <tr key={log.id}>
                        <td>{log.label}</td>
                        <td>{log.scenario}</td>
                        <td>{getVersionName(log.scenarioID)}</td>
                        <td>{log.userName}</td>
                        <td>{log.timestamp}</td>
                        <td>
                            <Link to={`/logs/${log.id}`}>Analyze</Link>&nbsp;&nbsp;&nbsp;&nbsp;
                            <Link to={`/comment/display/${log.id}`}>Start_Review</Link>&nbsp;&nbsp;&nbsp;&nbsp;
                            <Link to={`/observer/delete/${log.id}`}>Delete</Link>
                        </td>
                    </tr>
                ))}
            </tbody>

            <thead><tr><th>Existing Commented Logs</th></tr></thead>
            <tbody>
                <tr>
                    <th>Label</th>
                    <th>Scenario</th>
                    <th>Scenario_Version</th>
                    <th>Creator</th>
                    <th>Timestamp</th>
                </tr>

                {comments.map(log => (
                    <tr key={log.id}>
                        <td>{log.label}</td>
                        <td>{log.scenario}</td>
                        <td>{getVersionName(log.scenarioID)}</td>
                        <td>{log.commenter}</td>
                        <td>{log.timestamp}</td>
                        <td>
                            <Link to={`/comment/display/${log.id}`}>Continue_Review</Link>&nbsp;&nbsp;&nbsp;&nbsp;
                            <Link to={`/comment/delete/${log.id}`}>Delete</Link>
                        </td>
                    </tr>
                ))}
            </tbody>
        </Table>
    )

}