import { useState, useEffect } from 'react';

import { useFirebase } from "@meddbriefer/mdb-firebase"
import { getDebriefLog, getLog, verifyDocument, getPublishedScenarioObjectByID } from '@meddbriefer/scenario-data/index'
import {analyzeEvents} from '@meddbriefer/analysis'

import DisplayBaseDebriefing from './DisplayBaseDebriefing';


export default function Analyze({logID, context})  {
    const {db} = useFirebase()
    const [observerLog, setObserverLog] = useState (undefined)
    const [log, setLog] = useState(undefined)
    const [scenario, setScenario] = useState(undefined)
    const [analyzed, setAnalyzed] = useState(false)
    const [loaded, setLoaded] = useState(false)

    
    function checkOnOverwriting() {
        if (context === "review"){
            analyzeEventsHook(scenario, observerLog)
        }
        else {if (window.confirm ("Are you sure you want to overwrite the existing analysis?  If you aren't sure: cancel, start a review and save it.  This will save a copy of the current analysis in a different directory."))
            analyzeEventsHook(scenario, observerLog)
        }
    }

    async function analyzeEventsHook(scenario, log) {
      analyzeEvents(db, scenario, log)
    }

    
    async function processEvents ()  {
        if (! await verifyDocument(db, "debriefing-feedback-logs", logID))
            {analyzeEventsHook(scenario, observerLog)
            setAnalyzed(true)} 
        else {checkOnOverwriting()} 
        setAnalyzed(true)
        return (true)
    }

    useEffect(
        () => {
          getLog(db,logID)
            .then(observerLogData => {  
              setObserverLog(observerLogData)    
            })
            .catch(error => alert('error fetching observer log', error))
        // eslint-disable-next-line
      }, [db]
    )

      useEffect(
        () => {
          !!observerLog && getPublishedScenarioObjectByID(db, observerLog.scenarioID, "V2")
            .then(scenData => {
              console.log("retrieve scenario:", scenData)
              setScenario(scenData)
            })

        }, [db, observerLog]
      )

      useEffect(
        () => {
          !!scenario && processEvents()
        // eslint-disable-next-line
        }, [scenario]
      )

      useEffect(
        () => {
          !!analyzed && getDebriefLog(db, logID)
            .then(logData => {
              setLog(logData)  
            })
            .catch(error => alert('error fetching debriefing log', error))
        // eslint-disable-next-line
        }, [analyzed]
      )

      useEffect(
        () => {
          !!log && setLoaded(true)
        }, [log]
      )

      if (!loaded) {
        return (<h3>Loading</h3>)
      }

    return (
      <DisplayBaseDebriefing
        log={log}
        actions={log.events}
        scenario={scenario}
        context={context}
      />
    )
}
