import { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'

import { useFirebase } from "@meddbriefer/mdb-firebase"
import { getDebriefLog , getDebriefCommentLog, getPublishedScenarioObjectByID } from "@meddbriefer/scenario-data/index"

import DisplayBaseDebriefing from './DisplayBaseDebriefing'
import Analyze from './Analyze'

export default function DebLog() {
  const {db} = useFirebase()
  const { logID } = useParams()
  const [ log, setLog ] = useState(null)
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [ context, setContext ] = useState("review")
  const [ needAnalysisLog, setNeedAnalysisLog] = useState(null)
  const [ noAnalysisFound, setNoAnalysisFound] = useState(null)
  const [ scenario, setScenario] = useState(null)
  const [loaded, setLoaded] = useState(false)


  useEffect(
    () => {
      getDebriefCommentLog(db, logID)
        .then(logData => {
          setLog(logData) 
        })
        .catch(() => {
          setNeedAnalysisLog(true)
        })
    },
    [db, logID]
  )

  useEffect (
    () => {
      !!needAnalysisLog && getDebriefLog(db, logID)
        .then(logData => {
          setLog(logData) 
      })
      .catch(() => setNoAnalysisFound(true)  )
    },
    [db, logID, needAnalysisLog]
  )


  useEffect(
    () => {
      !!log && getPublishedScenarioObjectByID(db, log.scenarioID)
        .then(scenData => {
          setScenario(scenData)
        })
    }, [db, log]
  )

  useEffect(
    () => {
      !!scenario && setLoaded(true)
    }, [scenario]
  )

  /*if (!!noAnalysisFound){
     return (<p><h3>No Existing Analysis Found, Do Analysis First Then Return Here Afterwards To Review</h3>
      <LogList/></p>
    )} */

  if (noAnalysisFound){
    return (
      <Analyze
        logID={logID}
        context={context}
      />
    )
  }

  if (!loaded) {
    return (<h3>Loading</h3>)
  }
  return (
    <DisplayBaseDebriefing
      log={log}
      actions={log.events}
      scenario={scenario}
      context={context}
    />
  )

}

