import { useEffect} from 'react'
import { useParams, useHistory } from 'react-router-dom'

import { useFirebase } from "@meddbriefer/mdb-firebase"
import { deleteObsAndAnaDocuments } from "@meddbriefer/scenario-data/index"


export default function DeleteObsLogs() {
  const {db} = useFirebase()
  const { logID } = useParams()
  let history = useHistory()

  useEffect(
    () => {
      deleteObsAndAnaDocuments(db, logID)
      history.goBack()
    }, [db, logID, history]
  )

  return (null)
}